html {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  padding: 0;
  margin: 0;
}

html,
body {
  height: 100%;
  width: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.App {
  min-height: 94vh;
  max-width: 100%;
  padding: 0rem 2.5rem;
  background-color: #fdfdfd;
}



/*-------- Media Queries ---------*/

@media (max-width: 1000px) {

  .App {
    padding: 0rem 1rem
  }
}